.add_form {
  display: flex;
  padding: 1.2em;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}

.add_form input {
  flex: 1; /* 버튼을 뺀 빈 공간을 모두 채워줍니다 */
  font-size: 1.25rem;
  outline: none;
  border: none;
  border-bottom: 2px solid;
}

.add_form button {
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  padding-left: 1rem;
  padding-right: 1rem;
  margin-left: 1rem;
  background: #22b8cf;
  border-radius: 3px;
  color: white;
  font-weight: 600;
  cursor: pointer;
  border: none;
  outline: none;
}

.add_form button:hover {
  background: #3bc9db;
}
